//window.scroll functions
//show menubar at top of page, make the icon list static

window.addEventListener('scroll', debounce(fixTop, 16));
const tocCoords = toc.getBoundingClientRect();
const coords = { bottom: tocCoords.bottom + window.scrollY };

const header = document.querySelector('.content__header');
const headerHeight = header.getBoundingClientRect().height + 500;
const pageWrap = document.querySelector('article');
var lastScrollTop = 0;

function fixTop() {
  let currScrollTop = pageWrap.scrollTop;
  let isScrollingDown = currScrollTop > lastScrollTop;
  // console.log(isScrollingDown)
  let isHeaderVisible = currScrollTop < headerHeight;
  // console.log(isHeaderVisible)
  if (window.scrollY > coords.bottom) {
    // console.log(window.scrollY)
    toc.classList.add('fix-top');
    setTimeout(function() {
      toc.classList.add('fix-top-open');
    }, 0);
  } else if (window.scrollY < coords.bottom) {
    toc.classList.remove('fix-top');
    toc.classList.remove('fix-top-open');
  }
}

function debounce(func, wait) {
  let timeout;
  return function(...args) {
    const context = this;
    clearTimeout(timeout);
    timeout = setTimeout(() => func.apply(context, args), wait);
  };
}

